import React from 'react'
import './Home.css'
import { GrDashboard  ,GrTasks,GrUserSettings  } from 'react-icons/gr';
import {BiMessageRoundedCheck} from 'react-icons/bi';
import {SlCalender} from 'react-icons/sl'
const Home = () => {
  return (
    <div>

        <div className="sidebar">
            <div className="title">
                <span className="logo"></span>
                <span>Company Name</span>
            </div>
            <div className="dashboard"><GrDashboard/>Dashboard</div>
            <div className="tasks"><GrTasks/>Tasks</div>
            <div className="users"><GrUserSettings/>User</div>
            <div className="message"><BiMessageRoundedCheck/>Messages</div>
            <div className="calender"><SlCalender/> Calender</div>
        </div>

    </div>

  )
}

export default Home